.single-variable-page {
     width: 100%;
     height: 95vh;

     &__wrapper {
          padding: 20px 10px;
          height: 100%;

          .var-item {
               display: block;
               text-align: center;

               &__name-column {
                    font-size: 35px;
                    text-decoration: none;
                    color: #000;
                    font-weight: 900;
               }

               &__desc-column {
                    margin-top: 50px;
                    padding: 0px 20px 0px 20px;
                    font-size: 25px;
                    line-height: 1.5em;
                    color: green;
               }
          }
     }
}